.btn{
    position: relative;
    width: 70px;
    height: 30px;
    font-size: 10px;
    background-color: #202231;
    color: #fff;
    border: none;
    cursor: pointer;
    transition: width 1s;
    border-radius: .5s;
}
.btn *{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    transition: opacity .5s;
}
.icon{
    opacity: 0;
}
.btn:focus{
    width: 30px;
    background-color: #44c08a;
    border-radius: 50%;
}
.btn:focus .text{
    opacity: 0;
}
.btn:focus .icon{
    opacity: 1;
    align-items: inherit;
    bottom: 30px;
    transition-delay: .5s;
}